import React from 'react';

const footer = () => {
    const footerStyle = {
        position: 'absolute',
        bottom: 0,
        width: '100%',
        height: '60px',
        lineHeight: '60px',
    };

return (
    <footer style={footerStyle} className="navbar-dark bg-dark">
        <div className="container text-right">
            <span className="text-muted">Crypto.</span>
        </div>
    </footer>
);
}

export default footer;