import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { useParams, Link } from "react-router-dom";
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer } from 'recharts';
import CryptoModal from './addCryptoModal';
import moment from 'moment';

const cryptoFull = (props) => {
    let { id } = useParams();
    const [crypto, setCrypto] = useState(0);
    const [history, setHistory] = useState(1);

    useEffect(() => {
        axios.get('https://api.coincap.io/v2/assets/' + id)
            .then((response) => {
                setCrypto(response.data.data);
            });
    }, []);

    useEffect(() => {
        axios.get(`https://api.coincap.io/v2/assets/${id}/history?interval=d1&start=${moment().hour(0).minutes(0).seconds(0).subtract(1, 'months').valueOf()}&end=${moment().valueOf()}`)
            .then((response) => {
                sortHistory(response.data.data);
            });
    }, []);

    const sortHistory = (historyData) => {
        let historyArr = [];

        Object.keys(historyData).map(function (key) {
            historyArr.push({ name: historyData[key].date, uv: historyData[key].priceUsd })
        });

        setHistory(historyArr);
    }

    const handleImageError = e => {
        e.target.src = '/assets/images/32/nologo.png';
    }

    const CustomTooltip = ({ active, payload, label }) => {
        if (active && payload && payload.length) {
            return (
                <>
                    <div className="custom-tooltip px-4 pt-3 pb-1">
                        <p><strong>Date:</strong> {moment(payload[0].payload.name).format('DD/MM/YYYY')}</p>
                        <p><strong>Value:</strong> ${parseFloat(payload[0].value).toFixed(2)}</p>
                    </div>
                </>
            );
        }

        return null;
    }

    return (
        <>
            <div className="row container-main py-5">
                <div className="col-6">
                    <h2>{crypto.name} <small>({crypto.symbol})</small></h2>
                    <img onError={handleImageError} className="mb-2" src={`/assets/images/32/${String(crypto.symbol).toLowerCase()}.png`} />
                    <h3>${parseFloat(crypto.priceUsd).toFixed(2)} (<span className={crypto.changePercent24Hr > 0 ? 'text-success' : 'text-danger'}>{parseFloat(crypto.changePercent24Hr).toFixed(2)}%</span>)</h3>
                    <CryptoModal details={crypto} />
                    <Link to="/"><p className="mt-5">Return home</p></Link>
                </div>
                <div className="col-6">
                    <div style={{ width: '100%', height: 300 }}>
                        <ResponsiveContainer>
                            <LineChart data={history} margin={{ top: 0, right: 20, bottom: 0, left: 25 }}>
                                <Tooltip content={<CustomTooltip />} />
                                <YAxis interval="preserveStartEnd" tickFormatter={tick => {
                                    return tick.toLocaleString('en-US', { style: 'currency', currency: 'USD', });
                                }} />
                                <Line type="monotone" dataKey="uv" stroke="#000000" strokeWidth={2} activeDot={{ r: 8 }} />
                            </LineChart>
                        </ResponsiveContainer>
                    </div>
                </div>
            </div>
        </>
    );
}

export default cryptoFull;