import React, { useState, useEffect } from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";
import axios from 'axios';
import CryptoBasic from './components/cryptoBasic';
import CryptoFull from './components/cryptoFull';
import Navbar from './components/navbar';
import Footer from './components/footer';
import Login from './components/login';
import Register from './components/register';
import { w3cwebsocket as W3CWebSocket } from "websocket";
import './main.css';

const client = new W3CWebSocket('wss://ws.coincap.io/prices?assets=ALL');

const App = () => {
  const [crypto, setCrypto] = useState(0);
  const [btcPrice, setBtcPrice] = useState(1);
  const [limit, setLimit] = useState(10);

  useEffect(() => {
    axios.get('https://api.coincap.io/v2/assets?limit=' + limit)
      .then((response) => {
        setCrypto(response.data.data);
      });
  }, [limit]);

  useEffect(() => {
    client.onopen = () => {
      console.log('WebSocket Client Connected');
    };
    client.onmessage = (message) => {
      setBtcPrice(JSON.parse(message.data));
    };
  }, []);



  return (
    <Router basename={'/giacap'}>
      <Navbar />
      <div className="container page-wrapper">
        <div className="row text-center">
          <div className="col">

            <Switch>
              <Route exact path={`/login`}>
                <Login />
              </Route>
              <Route exact path={`/register`}>
                <Register />
              </Route>
              <Route exact path={`/`}>
                <div className="container-main">

                  <table className="table table-sm table-hover">
                    <thead>
                      <tr>
                        <th></th>
                        <th>Name</th>
                        <th>Rank</th>
                        <th>Value (USD)</th>
                        <th>24H %</th>
                        <th>7D Value</th>
                        <th>7D %</th>
                      </tr>
                    </thead>
                    <tbody>
                      {Object.keys(crypto).map(function (key) {
                        return <CryptoBasic key={key} details={crypto[key]} price={btcPrice[crypto[key].id]} />;
                      })}
                    </tbody>
                  </table>
                </div>
                <button onClick={() => setLimit(limit + 10)} type="button" className="btn btn-primary mt-3">Load more</button>
              </Route>
              <Route path={`/:id`} children={<CryptoFull />} />
            </Switch>
          </div>
        </div>
      </div>
      <Footer />
    </Router>
  );
}

export default App;
