import React, { useState } from 'react';
import Modal from 'react-bootstrap/Modal';


const addCryptoModal = (props) => {
    const [show, setShow] = useState(false);

    const handleClose = () => setShow(false);
    const handleShow = () => {
        if (!show) {
            setShow(true);
        }
    }

    return (
        <>
            <button onClick={handleShow} className="btn btn-primary mt-2">Add {props.details.name} to my giaCap wallet</button>
            <Modal show={show} onHide={handleClose} centered>
                <Modal.Header closeButton>
                    <Modal.Title>Add {props.details.name} to my giaCap wallet</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <div class="mb-3">
                        <label for="number-input" class="form-label">How much {props.details.name} are you HODLing?</label>
                        <input class="form-control" type="number" id="number-input" />
                    </div>
                </Modal.Body>
                <Modal.Footer>
                    <button className="btn btn-secondary" onClick={handleClose}>Close</button>
                    <button className="btn btn-primary" onClick={handleClose}>Save</button>
                </Modal.Footer>
            </Modal>
        </>
    );
}

export default addCryptoModal;