import React, { useState, useEffect } from 'react';
import { Link } from "react-router-dom";
import axios from 'axios';
import "./cryptoBasic.css";
import moment from 'moment';

const cryptoBasic = (props) => {
    const [week, setWeek] = useState('');
    const [price, setPrice] = useState('');
    const [prevPrice, setPrevPrice] = useState('');
    const [flash, setFlash] = useState('');
    const [sevenDay, setSevenDay] = useState('');

    useEffect(() => {
        if (localStorage.getItem(props.details.id) === null) {
            axios.get(`https://api.coincap.io/v2/assets/${props.details.id}/history?interval=d1&start=${moment().hour(0).minutes(0).seconds(0).subtract(7, 'days').valueOf()}&end=${moment().hour(23).minutes(59).seconds(59).subtract(7, 'days').valueOf()}`)
                .then((response) => {
                    setWeek(parseFloat(response.data.data[0].priceUsd));
                    localStorage.setItem(props.details.id, parseFloat(response.data.data[0].priceUsd));
                });
        }
        else {
            setWeek(localStorage.getItem(props.details.id));
        }

    }, []);

    useEffect(() => {
        if (price.length == 0) {
            setPrice(props.details.priceUsd);
        }
        else if (!isNaN(props.price)) {
            setPrevPrice(parseFloat(price).toFixed(2));
            setPrice(parseFloat(props.price).toFixed(2));

            if (price > prevPrice) {
                setFlash('1');
                setTimeout(() => {
                    setFlash('0');
                }, 1200);
            }
            else if (price < prevPrice) {
                setFlash('2');
                setTimeout(() => {
                    setFlash('0');
                }, 1200);
            }
        }
    }, [props.price]);

    useEffect(() => {
        setSevenDay(parseFloat(week > parseFloat(props.details.priceUsd) ? '-' + (week - parseFloat(props.details.priceUsd)) / parseFloat(props.details.priceUsd) * 100 : (parseFloat(props.details.priceUsd) - week) / parseFloat(props.details.priceUsd) * 100).toFixed(2));
    },[week]);

    const handleImageError = e => {
        e.target.src = '/assets/images/32/nologo.png';
    }

    return (
        <tr className={`crypto-row${flash == '1' ? " flash-success" : (flash == '2' ? " flash-danger" : "")}`}>
            <td className="align-middle"><img onError={handleImageError} src={`/assets/images/32/${props.details.symbol.toLowerCase()}.png`} /></td>
            <td className="align-middle"><Link to={{pathname:`/${props.details.id}`, state: {currentPrice: price }}}><strong>{props.details.name}</strong><br /><small>{props.details.symbol}</small></Link></td>
            <td className="align-middle">{props.details.rank}</td>
            <td className="align-middle">${parseFloat(price).toFixed(2)}</td>
            <td className={props.details.changePercent24Hr > 0 ? 'text-success align-middle' : 'text-danger align-middle'}>{parseFloat(props.details.changePercent24Hr).toFixed(2)}%</td>
            <td className="align-middle">${parseFloat(week).toFixed(2)}</td>
            <td className={sevenDay > 0 ? 'text-success align-middle' : 'text-danger align-middle'}>{sevenDay}%</td>
        </tr>
    );
}

export default cryptoBasic;